<template>
    <div ref="main" @scroll="showScrollTop" class="objectList">
        <div style="z-index:0" class="objectListBody">
            <div class="objectListSearch">
                <div style="z-index:1" class="objectListSHead">
                    <h5>Je rechercehe un objet</h5>
                    <div class="objectListWhere">
                        <span>Perdu où ?</span>
                        <div>
                            <input ref="quaterInput" style="padding-left:10px"
                            v-model="infos.quater" class="objectListInput" type="text" 
                            autocomplete="off" placeholder="Quartier"/>
                        </div>
                        <div ref="'objectListInputS'" class="objectListCities">
                            <InputSearch ref="objectListCities" type="Ville" :id="'objectList'"  
                                @newVal="(val)=>search('city',val)" :placeholder="'Saisir la ville'" />
                        </div>
                    </div>
                    <div class="objectListWhen"><span>Quand ?</span>
                        <input @change="search('quand1',infos.quand1)" v-model="infos.quand1" class="objectListInput" type="date" placeholder="min" style="margin-right:5px"/>-
                        <input @change="search('quand2',infos.quand2)" v-model="infos.quand2" class="objectListInput" type="date" placeholder="max" style="margin-left:5px"/>
                    </div>
                    <div class="objectListType"><span>Type:</span> 
                        <select @change="search('type1',infos.type1)" v-model="infos.type1">
                            <option v-for="(option,index) in this.$store.state.types" :key="index">
                                {{option}}
                            </option>
                        </select>
                    </div>
                </div>
                <div style="z-index:0" class="servicesMain">
                    <Service :elements='infos.listSort' :amount='4' class="landSlider0"/>
                    <Service :elements='infos.listSort' :amount='3' class="landSlider1"/>
                    <Service :elements='infos.listSort' :amount='2' class="landSlider2"/>
                    <Service :elements='infos.listSort' :amount='1' class="landSlider3"/>
                </div>
            </div>
        

        </div>

        <div class="objectListFooter"><JFooter/></div>
        <JNavbar
            ref='navbar'
            @search="(toSearch)=>search('search',toSearch)"
            :state="infos.type" @type="(newType)=>search('type',newType)"
            @inscription='$refs.inscription.Display=true'
            @connexion='$refs.connexion.Display=true'
            @iLost='$refs.stepsFormLost.Display=true'
            @iFound='$refs.stepsFormFound.Display=true'
        />            
        <StepsForm ref='stepsFormLost' type="lost" @connexion='$refs.connexion.Display=true'/>
        <StepsForm ref='stepsFormFound' type="found" @connexion='$refs.connexion.Display=true'/>
        <StepsForm ref='stepsFormEditF' type="found" edit="yes"/>
        <StepsForm ref='stepsFormEditL' type="lost" edit="yes"/>
        <Inscription ref='inscription' @connexion='$refs.connexion.Display=true'/>
        <Connexion ref='connexion' @inscription='$refs.inscription.Display=true'/>

        <ScrollTop ref="scrollTop" @click="scrollingTop" />
        <LLoading1 :visible="loading"/>
    </div>
</template>
<style src="./objectList.css">
</style>
<script src = "./objectList.js">
</script>
