import { createApp } from 'vue'
import App from './App.vue'
import router from './router'
import { createStore } from 'vuex'
import middleware from "@grafikri/vue-middleware"
const store = createStore({
    state () {
      return {
        //baseUrl: 'http://localhost:3000/',
        //baseUrl: 'http://192.168.56.92:3000/',
        baseUrl: 'https://rentalmanager.onrender.com/',
        types:["Montre","Chaussures","Téléphone","Ordinateur","Carte d'identité","Appareil électronique"],
        user: {
          connected: false,
          id:'',
          name:'',
          tel:''
        },
        currentObject:{id:-1,claimed:false},
        objectList:{
          created:false,
          list:[],
          listSort:[],
          type:'',
          srcoll:0,
          city:'Yaounde',quand1:'1950-01-01',quand2:'3000-01-01',
          type1:'',quater:'',search:''
        }
      }
    },
    mutations: {
      connexion (state,playload) {
        state.user.connected = playload.connected
        state.user.id = playload.id
        state.user.name = playload.name
        state.user.tel = playload.tel 
      },
      setCurrentObject (state,playload) {
        state.currentObject = playload
      },
      setObjectList (state,playload) {
        state.objectList.created = playload.created
        state.objectList.list = playload.list
        state.objectList.listSort = playload.listSort
        state.objectList.type = playload.type
        state.objectList.scroll = playload.scroll
        state.objectList.city = playload.city
        state.objectList.quand1 = playload.quand1
        state.objectList.quand2 = playload.quand2
        state.objectList.type1 = playload.type1
        state.objectList.quater = playload.quater
        state.objectList.search = playload.search
      }
    }
  })

const app = createApp(App)
router.beforeEach(middleware({store}))
app.use(router)
app.use(store)
app.mount('#app')
