import ImgUpload from '@/components/global/imgUpload/ImgUpload'
import InputSearch from '@/components/global/inputSearch/InputSearch'
import AlertG from '@/components/global/alertG/AlertG'
import LLoading1 from '@/components/global/lLoading1/LLoading1'

export default {
  name: 'lLoading',
  props: {
    type: {
      default:'lost',
      type:String
    },
    edit: {
      default:'not',
      type:String
    }
  },
  emits:["connexion"],
  components: {
    ImgUpload,
    InputSearch,
    AlertG,
    LLoading1
  },
  computed: {
    slides () {
      return this.type == 'lost' ? this.slidesL : this.slidesF
    },
    Position () {
      return this.position + '%'
    }
  },
  data () {
    return {
      loading:false,
      Display:false,
      position:0,
      transition:'left 0.7s ease-out',
      infos:{id:-1},
      toSend0:{},
      toSend1:{},
      toSend2:{},
      slidesL: [
        {
          title:"Qu'est ce que vous avez perdu ?",
          subtitle:"Veillez remplir les informations pertinente pour aider",
          selects:[
            {name:"type", value:"Téléphone",options:this.$store.state.types}
          ],
          city:false,
          inputs: [
            {name:"ownerName",type:"text", value:"",placeholder:"Nom du propriétaire"}
          ],
          textareas: [
            {name:"description", value:"",placeholder:"Description"}
          ],
          images:[]
        },
        {
          title:"Où l'avez vous perdu?",
          subtitle:"Veillez remplir les informations pour aider",
          selects:[
          ],
          city:true,
          inputs: [
            {name:"quartier",type:"text", value:"",placeholder:"Quartier"},
            {name:"lieu",type:"text", value:"",placeholder:"Lieu"}
          ],
          textareas: [
          ],
          images:[]
        },
        {
          title:"Quand l'avez vous perdu?",
          subtitle:"Intervalle de temps entre la date au premier champ et la date au 2ème",
          selects:[
          ],
          city:false,
          inputs: [
            {name:"quand1",type:"date", value:"",placeholder:"début"},
            {name:"quan2",type:"date", value:"",placeholder:"fin"}
          ],
          textareas: [
          ],
          images:[]
        },
        {
          title:"Avez vous des images ?",
          subtitle:"Ajouter des images de ce que vous recherchez",
          city:false,
          selects:[
          ],
          inputs: [
          ],
          textareas: [
          ],
          images:[{name:'lost'},{name:'lost'},{name:'lost'},{name:'lost'}]
        }
      ],
    //------------------- slide found --------------------------------

    slidesF: [
      {
        title:"Qu'avez vous trouvé ?",
        subtitle:"Veillez remplir les informations pertinente pour aider",
        selects:[
          {name:"type", value:"Téléphone",options:this.$store.state.types}
        ],
        city:false,
        inputs: [
          {name:"ownerName",type:"text", value:"",placeholder:"Nom du propriétaire (si visible)"}
        ],
        textareas: [
          {name:"description", value:"",placeholder:"Description"}
        ],
        images:[]
      },
      {
        title:"Où l'avez vous trouvé ?",
        subtitle:"Veillez remplir les informations pour aider",
        selects:[
        ],
        city:true,
        inputs: [
          {name:"quartier",type:"text", value:"",placeholder:"Quartier"},
          {name:"lieu",type:"text", value:"",placeholder:"Lieu"}
        ],
        textareas: [
        ],
        images:[]
      },
      {
        title:"Quand l'avez vous trouvé ?",
        subtitle:"Intervalle de temps entre la date au premier champ et la date au 2ème",
        selects:[
        ],
        city:false,
        inputs: [
          {name:"quand1",type:"date", value:"",placeholder:"début"},
          {name:"quan2",type:"date", value:"",placeholder:"fin"}
        ],
        textareas: [
        ],
        images:[]
      },
      {
        title:"Avez vous des images ?",
        subtitle:"Ajouter des images de ce que vous recherchez",
        city:false,
        selects:[
        ],
        inputs: [
        ],
        textareas: [
        ],
        images:[{name:'lost'},{name:'lost'},{name:'lost'},{name:'lost'}]
      }
    ]


    }
  },
  methods: {
    touching (event) {
      let elt = document.getElementById('IstepsFormP'+this.type+this.edit)
      //alert(typeof elt.contains(event.target))
      this.$refs["stepsFormI"][0].dontClear = false
      if(!elt.contains(event.target)) {
        //alert('ff')
        this.$refs["stepsFormI"][0].blurred()
      }
    },
    move (i) {
      if ((this.position >= -200 && i === -1) || (this.position < 0 && i === 1)) {
        this.position += i * 100
      }
    },
    loadObject (elt) {
      this.infos = elt
      let slides = this.type == 'lost' ? this.slidesL : this.slidesF
      slides[0].selects[0].value = this.infos.type
      slides[0].inputs[0].value = this.infos.owner
      slides[0].textareas[0].value = this.infos.description
      this.$refs.stepsFormI[0].setDefault(this.infos.city)
      slides[1].inputs[0].value = this.infos.quater
      slides[1].inputs[1].value = this.infos.place
      slides[2].inputs[0].value = this.infos.when1
      slides[2].inputs[1].value = this.infos.when2
      let bsrl = this.$store.state.baseUrl + 'images/'
      this.$refs.images[0].url = this.infos.img0 == '' ? '' : (this.infos.img0.includes('://') ?'':bsrl) + this.infos.img0
      this.$refs.images[1].url = this.infos.img1 == '' ? '' : (this.infos.img1.includes('://') ?'':bsrl) + this.infos.img1
      this.$refs.images[2].url = this.infos.img2 == '' ? '' : (this.infos.img2.includes('://') ?'':bsrl) + this.infos.img2
      this.$refs.images[3].url = this.infos.img3 == '' ? '' : (this.infos.img3.includes('://') ?'':bsrl) + this.infos.img3
      this.Display = true
    },
    updateObject () {
      let slides = this.type == 'lost' ? this.slidesL : this.slidesF
      this.infos.type = slides[0].selects[0].value
      this.infos.owner = slides[0].inputs[0].value
      this.infos.description = slides[0].textareas[0].value
      this.infos.quater = slides[1].inputs[0].value
      this.infos.place = slides[1].inputs[1].value
      this.infos.when1 = slides[2].inputs[0].value
      this.infos.when2 = slides[2].inputs[1].value

      let bsrl = this.$store.state.baseUrl + 'images/'
      this.infos.img0 = this.$refs.images[0].url.replace(bsrl,'' )
      this.infos.img1 = this.$refs.images[1].url.replace(bsrl,'' )
      this.infos.img2 = this.$refs.images[2].url.replace(bsrl,'' )
      this.infos.img3 = this.$refs.images[3].url.replace(bsrl,'' )
    },
    close () {
      this.Display = false
      this.transition = 'none'
      this.position = 0
      this.transition = 'left 0.7s ease-out'
    },
    //------------ validation --------------------------------------
    validation () {
      let step = Math.abs(this.position/100)
      let slide = this.slides[step]

      if(step === 0) {
        let regex2 = /^.{15,350}$/
        if (!regex2.test(slide.textareas[0].value)) {
          if (slide.textareas[0].value.length < 15) this.$refs.alertG.displaying('Description trop courte')
          else this.$refs.alertG.displaying('Description trop longue')
          return false
        }
        this.toSend0 = {type:slide.selects[0].value,owner:slide.inputs[0].value,description:slide.textareas[0].value}
        this.move(-1)
        return true
      }
      else if(step === 1) {
        let regex2 = /^.{1,40}$/
        if (this.$refs.stepsFormI[0].getCity() == -1) {
          this.$refs.alertG.displaying('Veuillez choisr une ville')
          return false
        }
        if (!regex2.test(slide.inputs[0].value)) {
          this.$refs.alertG.displaying(slide.inputs[0].placeholder+' incorrect')
          return false
        }
        if (!regex2.test(slide.inputs[1].value)) {
          this.$refs.alertG.displaying(slide.inputs[1].placeholder+' incorrect')
          return false
        }
        this.toSend1 = {ville:this.$refs.stepsFormI[0].getCity(),quater:slide.inputs[0].value,place:slide.inputs[1].value,}
        this.move(-1)
        return true
      }
      else if(step === 2) {
        if (slide.inputs[0].value == '') {
          this.$refs.alertG.displaying('Première date incorrecte incorrect')
          return false
        }
        if (slide.inputs[1].value == '') {
          this.$refs.alertG.displaying('Deuxième date incorrecte incorrect')
          return false
        }
        let d1 = new Date(slide.inputs[0].value)
        let d2 = new Date(slide.inputs[1].value)
        let d = new Date()
        if (d1>d || d2 >d) {
          this.$refs.alertG.displaying('La date ne peut etre supérieur à la date actuelle')
          return false
        }
        if (d1>d2) {
          this.$refs.alertG.displaying('La première date ne peut etre supérieur à la deuxième')
          return false
        }
        this.toSend2 = {quand1:slide.inputs[0].value, quand2:slide.inputs[1].value}
        this.move(-1)
        return true
      }
      else if(step === 3) {
        //---- if not connected ----------
        if(!this.$store.state.user.connected) {
          this.$emit('connexion')
          return
        }
        const axios = require('axios')
        let i = 0
        let formData = new FormData()
        formData.append('aim', this.type)
        formData.append('userId', this.$store.state.user.id)
        formData.append('id', this.infos.id)
        if(this.infos.id != -1) {
          formData.append('imgName', this.infos.imgName)
          formData.append('img0', this.infos.img0)
          formData.append('img1', this.infos.img1)
          formData.append('img2', this.infos.img2)
          formData.append('img3', this.infos.img3)
        }
        //----- let's append objects properties -----------
        let toSend ={...this.toSend0,...this.toSend1,...this.toSend2}
        for(const index in toSend) {
          formData.append(index,toSend[index])
        }

        //---- let's append images -----------
        let images = this.$refs.images
        for(i=0;i<images.length;i++) {
          if(images[i].modified && images[i].url !='') {
            formData.append('image'+i, images[i].file)
          }
          // image to be deleted -------
          else if(images[i].modified && images[i].url == '') {
            formData.append('image'+i, 'toDelete')
          }
        }
        //--------- now sending the request ----------------------
        this.loading = true
        axios.post(this.$store.state.baseUrl + 'publication.php', formData, {
            headers: {
            'Content-Type': 'multipart/form-data'
            }
        })
        .then(() => {
          this.loading = false
          if(this.infos.id != -1) this.updateObject()
          else this.$emit('newElt')
          this.$refs.alertGC.displaying('Succes!')
        })
        .catch((error) => {
          console.log(error)
          this.loading = false  
        })
      }
    }
  }
}
