import SearchBar from './SearchBar'
import SearchBar1 from './SearchBar1'

export default {
  name: 'jNavBar',
  props: {
    state: {
      type: String,
      default: 'none'
    }
  },
  components: {
    SearchBar,
    SearchBar1
  },
  emits:["inscription","connexion","iLost","iFound","type","search"],
  data () {
    return {
      connected:this.$store.state.user.connected
    }
  },
  methods: {
    setSearch (val) {
      this.$refs.search.search = val
      this.$refs.search1.search = val
    },
    toObjectList(type) {
        if(this.state == type) return
        this.$emit('type',type)
        if(this.state == 'none') this.$router.push({name:'objectList', params: {type: type }})
    },
    search (toSearch) {
      if(this.state == 'none') this.$router.push({name:'objectList', params: {search: toSearch }})
      else this.$emit('search',toSearch)
    },
    deconnexion () {
      this.setCookie('userId','',0)
      this.setCookie('userName','',0)
      this.setCookie('userTel','',0)
      let user = {id:'',name:'loilo',tel:'',connected:false}
      this.$store.commit('connexion',user)
    },
    setCookie(cname, cvalue, exdays) {
      const d = new Date();
      d.setTime(d.getTime() + (exdays * 24 * 60 * 60 * 1000));
      let expires = "expires="+d.toUTCString();
      document.cookie = cname + "=" + cvalue + ";" + expires + ";path=/";
    }
  },
  mounted () {
    this.$watch(
      () => this.$store.state.user.connected,
      (newVal) => {
        this.connected = newVal
      })
  }
}
