import LLoading from '@/components/global/lLoading/LLoading'
import AlertG from '@/components/global/alertG/AlertG'

export default {
  name: 'lFooter',
  props: {
    display:  {
      type:Boolean,
      default:false
    }
  },
  components: {
    LLoading,
    AlertG
  },
  data () {
    return {
      Display: false,
      type:"found",
      index:0,
      elts:[],
      loading:false,
      confIndex:0,
      objectId:0
    }
  },
  emits:["confirmed"],
  methods: {
    clicked (event) {
      if(event.target == this.$refs.main) this.Display = false
    },
    show (id,type,index) {
      this.Display = true
      this.loading = true
      this.objectId = id
      this.type = type
      this.index = index
      const axios = require('axios')
      axios.post(this.$store.state.baseUrl + 'publication.php', {
        aim:'claimElts',
        user:this.$store.state.user.id,
        object: id
      })
        .then(({data}) => {
          this.elts = data
          this.loading = false
        })
        .catch((error) => {
          console.log(error)
          this.loading = false
        })
    },
    reject (index) {
      this.loading = true
      const axios = require('axios')
      axios.post(this.$store.state.baseUrl + 'publication.php', {
        aim:'reject',
        id:this.elts[index].id
      })
      .then(() => {
        this.elts.splice(index,1)
        this.loading = false
      })
      .catch((error) => {
        console.log(error)
        this.loading = false
      })
    },
    confirmer (index) {
      this.loading = true
      const axios = require('axios')
      axios.post(this.$store.state.baseUrl + 'publication.php', {
        aim:'confirm',
        claimer: this.elts[index].claimer,
        object: this.objectId
      })
      .then(() => {
        this.$refs.alertG.displaying('Success !!')
        this.confIndex = index
        this.loading = false
      })
      .catch((error) => {
        console.log(error)
        this.loading = false
      })
    },
    confirmed () {
      this.$emit('confirmed',{index:this.index,type:this.type,
        name:this.elts[this.confIndex].name,tel:this.elts[this.confIndex].tel})
      this.Display = false
    }
  }
}
