import JNavbar from '@/components/global/jNavbar/JNavbar'
import JFooter from '@/components/global/jFooter/JFooter'
import Inscription from '@/components/inscription/Inscription'
import Connexion from '@/components/inscription/Connexion'
import RecoverAccount from '@/components/inscription/RecoverAccount'
import StepsForm from '@/components/global/stepsForm/StepsForm'
import MyProfile from '@/components/global/myProfile/MyProfile'
import LLoading1 from '@/components/global/lLoading1/LLoading1'
import LLoading from '@/components/global/lLoading/LLoading'
import Contact from '@/components/global/contact/Contact'

export default {
  name: 'objectList',
  components: {
    JNavbar,
    JFooter,
    Inscription,
    Connexion,
    RecoverAccount,
    StepsForm,
    MyProfile,
    LLoading1,
    LLoading,
    Contact
  },
  data () {
    return {
      loading1:false,
      loading:false,
      infos:{when1:'2000-01-01',when2:'2000-01-01',creationDate:'2000-01-01'},
      images:[],
      claimed:true,
      mois: ['Janvier','Février','Mars','Avril','Mai','Juin','Juillet',
      'Aout','Septembre','Octobre','Novembre','Décembre']
    }
 },
 methods: {
  setDate (dat) {
    let d = new Date(dat)
    return d.getDate()+' '+this.mois[d.getMonth()] +' '+ d.getFullYear()
  },
  setImages () {
    let bUrl = this.$store.state.baseUrl + 'images/'
    if(this.infos.img0 != '' ) this.images.push(bUrl+this.infos.img0)
    if(this.infos.img1 != '' ) this.images.push(bUrl+this.infos.img1)
    if(this.infos.img2 != '' ) this.images.push(bUrl+this.infos.img2)
    if(this.infos.img3 != '' ) this.images.push(bUrl+this.infos.img3)
  },
  showContact () {
    if(this.$store.state.user.connected) this.$refs.contact.displaying()
    else this.$refs.connexion.Display=true
  },
  unclaim () {
    if(!this.$store.state.user.connected) return
    this.loading = true
    const axios = require('axios')
    axios.post(this.$store.state.baseUrl + 'publication.php', {
      aim:'unclaim',
      object:this.infos.id,
      user:this.$store.state.user.id
    })
    .then(() => {
      this.loading = false
      this.infos.claimed = false
    })
    .catch((error) => {
      console.log(error)
      this.loading = false
    })
  },
  claim () {
    if(!this.$store.state.user.connected) {
      this.$refs.connexion.Display=true
      return
    }
    this.loading = true
    const axios = require('axios')
    axios.post(this.$store.state.baseUrl + 'publication.php', {
      aim:'claim',
      object:this.infos.id,
      user:this.$store.state.user.id
    })
    .then(() => {
      this.loading = false
      this.infos.claimed = true
    })
    .catch((error) => {
      console.log(error)
      this.loading = false
    })
  },
  isClaimed () {
    if(!this.$store.state.user.connected) return
    this.loading = true
    const axios = require('axios')
    axios.post(this.$store.state.baseUrl + 'publication.php', {
      aim:'isClaimed',
      object:this.infos.id,
      user:this.$store.state.user.id
    })
    .then(({data}) => {
      console.log(data)
      if(data) this.infos.claimed = true
      this.loading = false
    })
    .catch((error) => {
      console.log(error)
      this.loading = false
    })
  },
  getObject () {
    this.loading1 = true
    const axios = require('axios')
    axios.post(this.$store.state.baseUrl + 'publication.php', {
      aim:'getOne',
      id:this.$route.params.id
    })
      .then(({data}) => {
        this.infos = {...data,claimed:false}
        this.setImages()
        this.loading1 = false
        this.isClaimed()
      })
      .catch((error) => {
        console.log(error)
        this.loading1 = false
      })
  }
 },
 mounted () {
    this.$watch(
      () => this.$store.state.user.connected,
      (newVal) => {
        if(newVal) this.isClaimed()
        else this.infos.claimed = false
      })


    if(this.$store.state.currentObject.id != -1 ) {
      this.infos = this.$store.state.currentObject
      this.setImages()
      //console.log(this.infos)
      this.isClaimed()
    }
    else this.getObject()
  }
}
