import InputI0 from '@/components/inscription/InputI0.vue'
import ButtonI0 from '@/components/inscription/ButtonI0.vue'
import Infos from '@/components/global/infos/Infos'
import LLoading1 from '@/components/global/lLoading1/LLoading1'
import AlertG from '@/components/global/alertG/AlertG'
export default {
  name: 'inscription',
  components: {
    InputI0,
    ButtonI0,
    Infos,
    LLoading1,
    AlertG
  },
  data () {
    return {
      loading:false,
      Display:false,
      DispErr: false,
      error:''
    }
  },
  emits: ['onnexion'],
  methods: {
    closing (event) {
      if( event.target == this.$refs.main ) this.Display = false
    },
    toConnexion () {
      this.$emit('connexion')
      this.Display = false
    },
    save () {
      if (!this.validation()) return
      this.DispErr = false
      this.loading = true
      const axios = require('axios')
      //axios.defaults.withCredentials = true
      axios.post(this.$store.state.baseUrl + 'inscription.php', {
        type:'inscription',
        name: this.$refs.surname.message,
        contact: this.$refs.contact.message,
        password: this.$refs.password.message
      })
        .then(({data}) => {
          this.loading = false
          if(data=='exist') {
            this.DispErr = true
            this.error = 'Compte déjà existant'
          }
          else {
            this.$refs.alertG.displaying('Compte créé avec success!')  
            this.setCookie('userId',data.id,5)
            this.setCookie('userName',data.name,5)
            this.setCookie('userTel',data.tel,5)
            let user = {id:data.id,name:data.name,tel:data.tel,connected:true}
            this.$store.commit('connexion',user)
          }
        })
        .catch((error) => {
          console.log(error)
          this.loading = false
        })
    },
    validateEmail (code) {
      this.loading = true
      const axios = require('axios')
      //axios.defaults.withCredentials = true
      axios.post(this.$store.state.baseUrl + 'inscription.php', {
        type:'validateEmail',
        code: code
      })
        .then(({data}) => {
          this.loading = false
          if(data === 'success') {
            this.$refs.alertG.displaying('Success')
            this.$router.push('/inscription2')
          }
          else this.$refs.alertG.displaying('le code est incorrect')
        })
        .catch((error) => {
          console.log(error)
          this.loading = false
        })
    },
    validation () {
      this.DispErr = false
      let regex2 = /^[0-9]{6,11}.$/
      if (this.$refs.surname.message == '') {
        this.error = 'Nom incorrect'
        this.DispErr = true
        return false
      }
      if (!regex2.test(this.$refs.contact.message)) {
        this.error = 'Numéro téléphone incorrect'
        this.DispErr = true
        return false
      }
      if (this.$refs.password.message == '') {
        this.error = 'Mot de passe incorrect'
        this.DispErr = true
        return false
      }
      if (this.$refs.password.message !== this.$refs.confirmpw.message) {
        this.error = 'Les mots de passe ne correspondent pas'
        this.DispErr = true
        return false
      }
      return true
    },
    //-- cookie useful functions ------------
    setCookie(cname, cvalue, exdays) {
      const d = new Date();
      d.setTime(d.getTime() + (exdays * 24 * 60 * 60 * 1000));
      let expires = "expires="+d.toUTCString();
      document.cookie = cname + "=" + cvalue + ";" + expires + ";path=/";
    }
  }
}
