import JNavbar from '@/components/global/jNavbar/JNavbar'
import RCaroussel from '@/components/global/rCaroussel/RCaroussel'
import JFooter from '@/components/global/jFooter/JFooter'
import Inscription from '@/components/inscription/Inscription'
import Connexion from '@/components/inscription/Connexion'
import RecoverAccount from '@/components/inscription/RecoverAccount'
import StepsForm from '@/components/global/stepsForm/StepsForm'
import LLoading1 from '@/components/global/lLoading1/LLoading1'
import Confirm from '@/components/global/confirm/Confirm'
import Closer from '@/components/global/closer/Closer'

export default {
  name: 'landing',
  components: {
    JNavbar,
    RCaroussel,
    JFooter,
    Inscription,
    Connexion,
    RecoverAccount,
    StepsForm,
    LLoading1,
    Confirm,
    Closer
  },
  data () {
    return {
      connected:this.$store.state.user.connected,
      loading:false,
      eltsFound:[],
      eltsLost:[],
      eltsClosed:[],
      founders:[],
      toDeleteIndex:0,
      toDeleteType:[]
    }
  },
  methods: {
    getObjects () {
      this.loading = true
      const axios = require('axios')
      axios.post(this.$store.state.baseUrl + 'publication.php', {
        aim:'get',
        id:this.$store.state.user.id
      })
        .then(({data}) => {
          this.loading = false
          this.eltsFound = data.found
          this.eltsLost = data.lost
          this.eltsClosed = data.closed
          this.founders = data.founders
        })
        .catch((error) => {
          console.log(error)
          this.loading = false
        })
    },
    deleting (typeElt, index) {
      this.toDeleteIndex = index
      this.toDeleteType = typeElt
      this.$refs.confirm.show('Ete vous sure de vouloir supprimer ?')
    },
    deleteMe (elts,index,verdict) {
      if(verdict == 'no') return
      this.loading = true
      const axios = require('axios')
      axios.post(this.$store.state.baseUrl + 'publication.php', {
        aim:'delete',
        id:elts[index].id
      })
        .then(() => {
          elts.splice(index,1)
          this.loading = false
        })
        .catch((error) => {
          console.log(error)
          this.loading = false
        })
    },
    edit (elt) {
      if(elt.kind == 'found') this.$refs.stepsFormEditF.loadObject(elt)
      else this.$refs.stepsFormEditL.loadObject(elt)
    },
    removeList(pl) {
      if(pl.type == 'found') {
        this.eltsClosed.push({...this.eltsFound[pl.index],name:pl.name,tel:pl.tel})
        this.eltsFound.splice(pl.index,1)
      }
      else {
        this.eltsClosed.push({...this.eltsLost[pl.index],name:pl.name,tel:pl.tel})
        this.eltsLost.splice(pl.index,1)
      }
    }
  },
  mounted () {
    this.$watch(
      () => this.$store.state.user.connected,
      (newVal) => {
        this.connected = newVal
        if(this.connected) this.getObjects()
      })

    if(this.$store.state.user.connected) this.getObjects()
  }
}
