import JNavbar from '@/components/global/jNavbar/JNavbar'
import JFooter from '@/components/global/jFooter/JFooter'
import Service from '@/components/service/Service'
import LLoading1 from '@/components/global/lLoading1/LLoading1'
import ScrollTop from '@/components/global/scrollTop/ScrollTop'
import InputSearch from '@/components/global/inputSearch/InputSearch'
import Inscription from '@/components/inscription/Inscription'
import Connexion from '@/components/inscription/Connexion'
import RecoverAccount from '@/components/inscription/RecoverAccount'
import StepsForm from '@/components/global/stepsForm/StepsForm'

export default {
  name: 'objectList',
  components: {
    JNavbar,
    JFooter,
    Service,
    LLoading1,
    ScrollTop,
    InputSearch,
    Inscription,
    Connexion,
    RecoverAccount,
    StepsForm
  },
  data () {
    return {
      loading:true,
      infos:{ 
          list:[],listSort:[],type:'found',scroll:0,
          city:'Yaounde',quand1:'',quand2:'',
          type1:'Téléphone',quater:'',search:''
        }
    }
 },
 methods: {
  scrollingTop () {
    this.$refs.main.scrollTo({top:0,behavior:'smooth'})
  },
  showScrollTop () {
    this.$refs.main.scrollTop > 700 ? this.$refs.scrollTop.show() : this.$refs.scrollTop.hide()
  },
  search (index,value) {
    this.infos[index] = value
    console.log(index+':'+value)
    this.getObjects()
  },
  searchByQuater (word) {
    let quater = this.removeAccents(word)
    let i = 0, result = []
    for(i=0; i<this.infos.list.length; i++){
        let str = this.removeAccents(this.infos.list[i].quater)
        if(str.includes(quater)) {
            result.push(this.infos.list[i])
        }
    }
    //--- updating------
    this.infos.listSort = []
    let k = this
    this.$nextTick(()=>{
      k.infos.listSort = result
    })
  },
  removeAccents (str) {
      return str.normalize('NFD').replace(/[\u0300-\u036f]/g, '').toLowerCase()
  },
  getObjects () {
    this.loading = true
    const axios = require('axios')
    axios.post(this.$store.state.baseUrl + 'publication.php', {
      aim:'sort',
      kind:this.infos.type,
      city:this.infos.city,
      type:this.infos.type1,
      quand1:this.infos.quand1 == '' ? '1950-01-01':this.infos.quand1,
      quand2:this.infos.quand2 == '' ? '3000-01-01':this.infos.quand2,
      search:this.infos.search
    })
      .then(({data}) => {
        //console.log(data)
        this.infos.scroll = 0
        this.$refs.main.scrollTo({top:0})
        //--- updating data
        let k = this
        k.infos.list = []
        k.infos.listSort = []
        this.$nextTick(()=>{
          k.infos.list = data
          k.infos.listSort = data
        })
        this.loading = false
      })
      .catch((error) => {
        console.log(error)
        this.loading = false
      })
  }
 },
 mounted () {
  let k = this
  let input = this.$refs.quaterInput
  input.addEventListener('input',function(event){
      let word = event.data === null ? '' : event.data
      if (typeof word === 'undefined' || word === null) word = ''
      input.blur()
      input.focus()
      k.searchByQuater(k.infos.quater)

  })
},
 beforeRouteEnter(to, from, next) {
  next ( vm => {
      vm.$refs.objectListCities.toSearch=vm.$store.state.objectList.city
      vm.$refs.navbar.setSearch(vm.$store.state.objectList.search)
      if(vm.$store.state.objectList.created && (typeof vm.$route.params.type == 'undefined')) {
          vm.infos = vm.$store.state.objectList
          vm.loading = false
          return false
      }
      else {
          vm.infos.type = typeof vm.$route.params.type != 'undefined' ? vm.$route.params.type : 'found'
          vm.infos.type1 = typeof vm.$route.params.type1 != 'undefined' ? vm.$route.params.type1 : 'Téléphone'
          vm.infos.search = typeof vm.$route.params.search != 'undefined' ? vm.$route.params.search : ''
          vm.$refs.navbar.setSearch(vm.infos.search)
          vm.getObjects()
      }
    })
  },
  beforeRouteLeave() {
      let newInfos = {...this.infos,claimed:false}
      newInfos.created = true
      newInfos.scroll = this.$refs.main.scrollTop
      this.$store.commit('setObjectList',newInfos)
  },
  updated () {
    window.scrollTo(0,0)
    setTimeout(()=> {this.$refs.main.scrollTo({top:this.infos.scroll,behavior:'smooth'})},0)
  }
}
