<template>
    <div class="object">
        <div class="objectBody">
            <div v-if="images.length > 0" class="objectImg"><MyProfile :images='images'/></div>
            <div class="objectInfos">
                <div><h4>Description</h4><span>{{ infos.description }}</span></div>
                <div><h4>{{infos.kind == 'lost' ? 'Perdu à' : 'Trouvé à'}}</h4>
                    <span>{{ infos.city +', '+infos.quater+', '+infos.place  }}.<br/>
                    Entre le {{ setDate(infos.when1) }} et le {{ setDate(infos.when2) }}</span></div>
                <div><h4>Type</h4><span>{{ infos.type }}</span></div>
                <div><h4>Propriétaire </h4><span>{{ infos.owner == '' ?'Pas d\'indications':infos.owner }}</span></div>
                    <span @click="loading ?'':claim()" v-show="!infos.claimed"> 
                        <LLoading :visible="loading"/>
                        {{ infos.kind == 'lost' ?'RENDRE OBJET AU PROPRIETAIRE':'RECLAMMER L\'OBJET' }}</span>
                    <span @click="loading ?'':unclaim()" v-show="infos.claimed" style="background-color:rgb(20,170,20)"> 
                        <LLoading :visible="loading"/>
                        {{ infos.kind == 'lost' ?'ANNULER RENDRE PROPRIETAIRE':'ANNULER RECLAMMER' }}
                    </span>
            </div>
            <div class="objectPublisher">
                <h4>Publié par</h4>
                <span>{{ infos.creator }} le {{ setDate(infos.creationDate) }}</span>
                <div @click="showContact">Le contacter</div>
            </div>
        </div>
        <div class="objectFooter"><JFooter/></div>

        <Contact ref="contact" :infos="{name:infos.creator, type:'Me contacter à',contact:infos.tel}"/>
        <JNavbar
            @inscription='$refs.inscription.Display=true'
            @connexion='$refs.connexion.Display=true'
            @iLost='$refs.stepsFormLost.Display=true'
            @iFound='$refs.stepsFormFound.Display=true'
        />            
        <StepsForm ref='stepsFormLost' type="lost" @connexion='$refs.connexion.Display=true'/>
        <StepsForm ref='stepsFormFound' type="found" @connexion='$refs.connexion.Display=true'/>
        <StepsForm ref='stepsFormEditF' type="found" edit="yes"/>
        <StepsForm ref='stepsFormEditL' type="lost" edit="yes"/>
        <Inscription ref='inscription' @connexion='$refs.connexion.Display=true'/>
        <Connexion ref='connexion' @inscription='$refs.inscription.Display=true'/>
        
        <LLoading1 :visible='loading1'/>
    </div>
</template>
<style src="./object.css">
</style>
<script src = "./object.js">
</script>
